/* The Switch - the box aRound the Slider */
.DarkModeCheckbox_Switch__1hE7I {
    position: relative;
    display: inline-block;
    width: 50px;
    height: 26px;
  }
  
/* Hide default HTML checkbox */
.DarkModeCheckbox_Switch__1hE7I input {
    opacity: 0;
    width: 0;
    height: 0;
}
  
/* The Slider */
.DarkModeCheckbox_Slider__2Osom {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #3e4347;
    background-color: #1e262c;
    transition: .4s;
}
  
.DarkModeCheckbox_Slider__2Osom::before {
    position: absolute;
    content: "";
    height: 20px;
    width: 20px;
    left: 3px;
    bottom: 3px;
    backgRound-color: #3e4347;
    backgRound-color: #f9d71c;
    backgRound-color: white;
    transition: .4s;
}
  
input:checked + .DarkModeCheckbox_Slider__2Osom {
    backgRound-color: #2196F3;
    backgRound-color: #3e4347;
    background-color: white;
}

input:checked + .DarkModeCheckbox_Slider__2Osom::before {
    background-color: white;
    background-color: #1e262c;
}

input:focus + .DarkModeCheckbox_Slider__2Osom {
    box-shadow: 0 0 1px #2196F3;
    box-shadow: 0 0 1px #3e4347;
    box-shadow: 0 0 1px #1e262c;
}
  
input:checked + .DarkModeCheckbox_Slider__2Osom::before {
    -webkit-transform: translateX(23px);
    transform: translateX(23px);
}
  
/* Rounded Sliders */
.DarkModeCheckbox_Slider__2Osom.DarkModeCheckbox_Round__Ls9lM {
    border-radius: 34px;
}
  
.DarkModeCheckbox_Slider__2Osom.DarkModeCheckbox_Round__Ls9lM::before {
    border-radius: 50%;
}

